.main-nav {
  display: flex;
  flex-direction: column;
  position: relative;
}

.main-nav__toggle {
  position: relative;
  display: block;
  width: 24px;
  height: 20px;
  margin-top: 12px;
  margin-right: 20px;
  padding: 0;
  border: none;
  background-image: url("../../img/header-menu.svg");
  background-repeat: no-repeat;
  background-color: #ffffff;
  cursor: pointer;
}

.main-nav__list {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 320px;
  margin-top: 66px;
  align-items: center;
}

.site-list__item {
  text-align: center;
  width: 100%;
  padding-top: 22px;
  padding-bottom: 24px;
  border-bottom: @border-bottom;
  background-color: #ffffff;
}

.site-list__link {
  text-decoration: none;
  font-family: 'Oswald';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
}

.main-nav__panel {
  width: 320px;
  height: 65px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-bottom: @border-bottom;
}
